@import url(https://fonts.googleapis.com/css?family=Lato);
h1 {
  color: #3CA1E0;
  text-transform: uppercase;
  font-size: 30px;
  font-family: 'Lato', sans-serif;
}

h3 {
  font-weight: 350;
}

h4 {
  font-weight: lighter;
}

p {
  font-weight: lighter;
}

.landingpage-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.presentation {
  width: 750px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px;
  padding: 20px 0px 45px 0px;
  border-bottom: 2px solid black;
}

.image-div {
  padding: 20px;
  width: 40%;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.image-div img {
  height: 350px;
  width: auto;
  object-fit: contain;
}

.image p {
  font-size: 13px;
}

.presentationtext {
  width: 60%;
  padding: 20px;
}

.tech-div {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 60px 0px;
}

.tech-div h3 {
  font-weight: lighter;
  line-height: 28px;
}

.work-div {
  width: 750px;
  display: flex;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 60px 0px;
  border-bottom: 2px solid black;
}

.skills-div {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 60px 0px;
  border-bottom: 2px solid black;
}

.studies-div {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 0px 60px 0px;
}

.workexample-container {
  width: 750px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px;
  padding: 0px 0px 60px 0px;
  border-bottom: 2px solid black;
}

.workexample {
  width: 330px;
  height: 330px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
  margin: 10px;
  background-color: #DBDDD8;
}
.workexample img {
  object-fit: contain;
  width: 330px;
  height: 280px;
  overflow: hidden;
}

.skills-container {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 0px 0px 60px 0px;
  border-bottom: 2px solid black;
}

.allskills {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.skill {
  width: 33.3%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: lighter;
}

.studies-container {
  width: 750px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 0px 0px 60px 0px;
}

.allstudies {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.study {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 23px;
  margin-bottom: 23px;
}

h4 {
  margin-top: -3px;
}

.footer {
  width: 100%;
  background-color: #fadbd6;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0px;
}

li {
  padding: 5px;
}

a {
  color: black;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #f8f3f0;
}

